:root {
  --color-primary: #2563eb;
  --color-white: #fff;
  --color-background: #f0f4f8;
  --color-text: #1f2937;
  --color-shadow-100: #0000000c;
  --color-shadow-300: #00000019;
  --color-shadow-500: #00000019;
  --color-shadow-800: #00000040;
  --font-family-sans: Arial, sans-serif;
  --font-weight-semibold: 600;
  --spacing-2: .5rem;
  --spacing-4: 1rem;
  --spacing-10: 2.5rem;
  --spacing-12: 3rem;
  --border-radius-sm: .25rem;
  --border-radius-md: .5rem;
  --border-radius-lg: .75rem;
  --shadow-xl: 0 20px 25px -5px var(--color-shadow-500), 0 8px 10px -6px var(--color-shadow-100);
  --shadow-2xl: 0 25px 50px -12px var(--color-shadow-800);
  --transition-duration: .15s;
  --transition-timing: ease-in-out;
}

@keyframes fadeInSlideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(.9);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

body {
  font-family: var(--font-family-sans);
  background-color: var(--color-background);
  color: var(--color-text);
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
  overflow: hidden;
}

.intro-container {
  background-color: var(--color-white);
  padding: var(--spacing-10);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-2xl);
  width: 100%;
  max-width: 800px;
  margin: var(--spacing-4);
  align-items: center;
  gap: var(--spacing-10);
  opacity: 0;
  flex-direction: column;
  animation: 1.2s ease-out .3s forwards fadeInSlideUp;
  display: flex;
}

.intro-content {
  text-align: center;
}

.intro-image {
  border-radius: var(--border-radius-md);
  width: 256px;
  height: auto;
  box-shadow: var(--shadow-xl);
  object-fit: cover;
  opacity: 0;
  animation: 1s ease-out forwards scaleIn;
}

.text-blue-600 {
  color: var(--color-primary);
}

.btn-custom {
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  font-weight: var(--font-weight-semibold);
  padding: var(--spacing-2) var(--spacing-4);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: background-color var(--transition-duration) var(--transition-timing), color var(--transition-duration) var(--transition-timing);
  background-color: #0000;
  text-decoration: none;
  display: inline-block;
}

.btn-custom:hover, .btn-custom:focus {
  background-color: var(--color-primary);
  color: var(--color-white);
}

@media (width > 768px) {
  .intro-container {
    padding: var(--spacing-12);
    gap: var(--spacing-12);
    flex-direction: row;
    align-items: center;
  }

  .intro-content {
    text-align: left;
  }

  .intro-image {
    flex-shrink: 0;
    width: 320px;
  }
}
